.header {
  position: relative;
  height: 100vh;
  min-height: 750px;
  padding: 20vh 10px 0
}

.header__title {
  font-size: 50px;
  font-weight: 900;
  margin: 0;
  letter-spacing: -2px;
  text-transform: uppercase;
  color: hsla(0, 0%, 100%, .3)
}

@media screen and (min-width:800px) {
  .header__title {
    font-size: 70px;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%)
  }
}