.about {
  padding: 30vh 20px 0;
  color: #fff
}

.about__wrapper {
  margin: 0 auto
}

.about__element-wrapper,
.about__title {
  margin-bottom: 20px
}

.about__title {
  font-size: 30px;
  font-weight: 900;
  text-transform: uppercase
}

.about__title--hello {
  color: #ffd747
}

.about__title--technologies {
  color: #fc6170
}

.about__title--experience {
  color: #8ceeee
}

.about__title--contacts {
  color: #ff8a47
}

.about__title--interests {
  color: #e59de6
}

.about__text {
  margin: 0
}

.about__text--email {
  font-size: 20px;
  font-weight: 400;
  color: #fff;
  outline: none
}

.about__text a {
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  text-decoration: none;
  color: hsla(0, 0%, 100%, .8)
}

.about__text a:focus,
.about__text a:hover {
  color: orange
}

.about__text p {
  color: hsla(0, 0%, 100%, .6)
}

.about__text p:first-of-type {
  margin-top: 0
}

.contacts__wrapper {
  font-size: 18px;
  margin: 0;
  padding: 0;
  list-style: none;
  color: #fff
}

.contacts__item {
  margin-bottom: 10px
}

.contacts__item a {
  outline: 0
}

.technologies__list,
.technologies__title {
  font-size: 20px;
  font-weight: 400;
  margin: 0 0 20px;
  padding: 0
}

.technologies__list {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: space-around;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center
}

.technologies__list .technologies__item {
  width: 150px;
  height: auto;
  margin-right: 30px;
  margin-bottom: 10px;
  cursor: pointer;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s
}

.technologies__list .technologies__item--animated,
.technologies__list .technologies__item:focus,
.technologies__list .technologies__item:hover {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2)
}



@media screen and (min-width:860px) {


  .about__wrapper {
    width: 60%
  }

  .about__element-wrapper {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 50px
  }

  .about__title {
    width: 50%
  }

  .about__text {
    width: 60%
  }
}

