.good-luck__wish {
  position: relative;
}

.good-luck__wish::after {
  position: absolute;
  top: 0;
  right: -2px;

  display: block;

  width: 2px;
  height: 100%;

  content: '';
  animation-name: cursorBeeping;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes cursorBeeping {
  0% {
    background-color: rgba(255, 255, 255, .5);
  }

  49% {
    background-color: rgba(255, 255, 255, .5);
  }

  50% {
    background-color: transparent;
  }
}