.nav {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
}

.nav__list {
  font-size: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  /* display: none; */
  width: 100%;
  -webkit-transition: opacity .4s;
  -o-transition: opacity .4s;
  transition: opacity .4s;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 0;
  color: #fff
}

.nav,
.nav__burger {
  background-color: transparent;
}

.nav--opened {
  animation-name: openMenu;
  animation-duration: 2s;
  animation-direction: normal;
  animation-fill-mode: forwards;
}

.nav--opened .nav__list {
  opacity: 0;
  display: block;

  animation-name: showList;
  animation-duration: 2s;
  animation-direction: normal;
  animation-fill-mode: forwards;
}

.nav--closed {
  animation-name: closeMenu;
  animation-duration: 2s;
  animation-direction: reverse;
  animation-fill-mode: backwards;
}

.nav--closed .nav__list {
  animation-name: hideList;
  animation-duration: 2s;
  animation-fill-mode: both;
  animation-direction: normal;
  /* display: block; */
  /* display: none; */
}

@keyframes closeMenu {
  0% {
    background-color: #141414;
    width: 100px;
    height: 100px;
  }

  50% {
    width: 100%;
    height: 100px;
  }

  100% {
    width: 100%;
    height: 100vh;
    background-color: #141414;
  }
}

@keyframes openMenu {
  0% {
    background-color: #141414;
    width: 100px;
    height: 100px;
  }

  50% {
    width: 100%;
    height: 100px;
  }

  100% {
    width: 100%;
    height: 100vh;
    background-color: #141414;
  }
}

@keyframes showList {
  45% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes hideList {
  0% {
    opacity: 1;
    display: block;
  }

  30% {
    display: block;
    opacity: 0;
  }

  100% {
    display: none;
    opacity: 0;
  }
}

.nav__burger {
  font-size: 0;
  position: absolute;
  top: 34px;
  left: 30px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 40px;
  height: 32px;
  padding: 0;
  border: 0;
  outline: 0;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between
}

.nav__burger--active {
  height: 40px
}

.nav__burger--active .nav__burger-stripe--first {
  -webkit-transform: translateY(18px) rotate(225deg);
  -ms-transform: translateY(18px) rotate(225deg);
  transform: translateY(18px) rotate(225deg)
}

.nav__burger--active .nav__burger-stripe--second {
  display: none
}

.nav__burger--active .nav__burger-stripe--third {
  -webkit-transform: translateY(-18px) rotate(-225deg);
  -ms-transform: translateY(-18px) rotate(-225deg);
  transform: translateY(-18px) rotate(-225deg)
}

.nav__burger:active .nav__burger-stripe,
.nav__burger:hover .nav__burger-stripe {
  background-color: orange
}

.nav__burger:not(.nav__burger--active):active .nav__burger-stripe,
.nav__burger:not(.nav__burger--active):hover .nav__burger-stripe {
  -webkit-box-shadow: none;
  box-shadow: none
}

.nav__burger:not(.nav__burger--active):active .nav__burger-stripe--first,
.nav__burger:not(.nav__burger--active):hover .nav__burger-stripe--first {
  -webkit-transform: translateY(28px);
  -ms-transform: translateY(28px);
  transform: translateY(28px)
}

.nav__burger:not(.nav__burger--active):active .nav__burger-stripe--third,
.nav__burger:not(.nav__burger--active):hover .nav__burger-stripe--third {
  -webkit-transform: translateY(-28px);
  -ms-transform: translateY(-28px);
  transform: translateY(-28px)
}

.nav__burger-stripe {
  display: inline-block;
  width: 40px;
  height: 4px;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  background-color: #fff;
  -webkit-box-shadow: #141414 0 0 2px;
  box-shadow: 0 0 2px #141414
}

.nav__list,
.nav__projects-list {
  margin: 0;
  padding: 0;
  list-style: none
}

.nav__item {
  margin-bottom: 30px;
  text-align: center
}

.nav__projects-item {
  margin-bottom: 10px
}

.nav__item-text {
  margin: 0
}

.nav__link,
.nav__projects-link {
  font-weight: 400;
  -webkit-transition: color .3s;
  -o-transition: color .3s;
  transition: color .3s;
  text-decoration: none;
  color: #fff;
  outline: none
}

.nav__link--active,
.nav__link:focus,
.nav__link:hover,
.nav__projects-link--active,
.nav__projects-link:focus,
.nav__projects-link:hover {
  color: orange
}