.works {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  overflow-x: hidden;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between
}

.works__tag {
  font-size: 40px;
  font-weight: 300;
  width: 100%;
  margin-top: 0;
  text-align: center;
  color: hsla(0, 0%, 100%, .5)
}

