.project {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 50vh;
  margin-bottom: 40px;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transform: translate(0);
  -ms-transform: translate(0);
  transform: translate(0)
}

.project:focus,
.project:hover {
  z-index: 2
}

.project:focus .project__img,
.project:hover .project__img {
  -webkit-filter: blur(8px);
  filter: blur(8px)
}

.project:focus .project__hover,
.project:hover .project__hover {
  opacity: 1
}



.project__link {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  text-decoration: none;
  outline: none
}

.project__img {
  position: relative;
  -webkit-transform: translate(0) scale(1);
  -ms-transform: translate(0) scale(1);
  transform: translate(0) scale(1);
  -o-object-fit: cover;
  object-fit: cover
}

.project__img,
.project__img img {
  width: 100%
}

.project__hover,
.project__img,
.project__img img {
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s
}

.project__hover {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 50vh;
  text-align: center;
  opacity: 0;
  color: #fff;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center
}

.project__title {
  font-size: 35px;
  font-weight: 900;
  margin-top: 0;
  margin-bottom: 20px;
  text-transform: uppercase
}

.project__description {
  font-size: 20px;
  font-weight: 400;
  max-width: 90%;
  margin: 0 auto;
  text-align: center
}

.project__type {
  font-size: 25px;
  font-weight: 900;
  display: none;
  margin: 0 0 20px;
  text-transform: uppercase
}

.project__type--new {
  display: block;
  color: #ff5357
}

.project__type--underDevelopment,
.project__type--upgraded {
  display: block;
  color: #00ca74
}

@media screen and (min-width:860px) {
  .project,
  .project__hover {
    height: 70vh
  }
}

@media screen and (min-width:1100px) {
  .project--half {
    width: calc((100vw - 40px)/2)
  }

  .project--half:first-of-type {
    margin-right: 40px
  }
}